.card {
  display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #fff;
    padding: 12px;
    border-radius: 4rem;
    box-shadow: 3px 3px 3px rgb(18 8 8);
    margin-bottom: 0.5rem;
    margin-left: 20.5rem;
    margin-right: 20.5rem;
}

.card input[type="text"] {
  margin-right: 12px;
  font-size: 1.0rem;
    margin-left: 32px;
}

.card select {
  width: 100px;
  margin-right: 21px;
  border-radius: 3rem;
  padding-left: 0.5rem;
  font-size: 1.0rem;
}

.card nameStyle{
  border-radius: 2rem;
}

.card .iconHolder {
  display: flex;
  justify-content: center;
  align-items: center;
}

.card .iconHolder i {
  cursor: pointer;
}
