
.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  border-radius: 4px;
  padding: 12px;
  padding-left: 24px;
  background: linear-gradient(146deg, #e1eeff 19.67%, #ffe4fb 127.56%);
  margin-bottom: 3.5rem;
}


.navbarItem{
  font-size: 2.1rem;
  margin-block-start: 0.5em;
  padding-left: 6.5rem;
  font-size: 24px;
  font-weight: 700;
  
}

.material-icons{
  align-items: end;
  padding-right: 6.5rem;
}
.header  i {
  cursor: pointer;
}